import * as React from 'react';
import * as ReactDom from 'react-dom';
import { Version } from '@microsoft/sp-core-library';
import {
  BaseClientSideWebPart,
  IPropertyPaneConfiguration,
  PropertyPaneTextField
} from '@microsoft/sp-webpart-base';

import * as strings from 'WpDocumentLibInfoWebPartStrings';
import WpDocumentLibInfo from './components/WpDocumentLibInfo';
import { IWpDocumentLibInfoProps } from './components/IWpDocumentLibInfoProps';

export default class WpDocumentLibInfoWebPart extends BaseClientSideWebPart<IWpDocumentLibInfoProps> {

  public render(): void {
    const element: React.ReactElement<IWpDocumentLibInfoProps > = React.createElement(
      WpDocumentLibInfo,
      {
        description: this.properties.description,
        siteurl:this.context.pageContext.web.absoluteUrl,
        spHttpClient: this.context.spHttpClient,
        DocumentLibraryName:this.properties.description,
      }
    );

    ReactDom.render(element, this.domElement);
  }

  protected get dataVersion(): Version {
    return Version.parse('1.0');
  }

  protected getPropertyPaneConfiguration(): IPropertyPaneConfiguration {
    return {
      pages: [
        {
          header: {
            description: strings.PropertyPaneDescription
          },
          groups: [
            {
              groupName: strings.BasicGroupName,
              groupFields: [
                PropertyPaneTextField('description', {
                  label: strings.DescriptionFieldLabel
                }),
                
              ]
            }
          ]
        }
      ]
    };
  }

}
